@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

:root {
  --font-roboto: "Roboto Slab", serif;
  --font-raleway: "Raleway", sans-serif;
  --border: 1px solid rgb(51, 49, 49);
}

body {
  font-family: var(--font-roboto);
  background-color: #212426;
  color: #d6c1b0;

}

.container {
  padding: 1rem 12rem;
  width: 100%;
  margin-top: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.app1 {
  padding: 1rem 12rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  border: 0px solid rgb(17, 179, 147);
}

h1 {
  font-size: 3rem;
  letter-spacing: 0.9px;
  background: linear-gradient(130deg,
      rgba(249, 211, 180, 1) 0%,
      rgba(249, 211, 180, 0) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  font-weight: 800;

}

.search {
  width: 700px;
  margin: 1rem 0 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 1rem;
  border-radius: 3rem;
  background: #1f2123;
  -webkit-box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
  box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
}

.search input[type=text] {
  flex: 1;
  font-size: 2rem;
  font-family: var(--font-raleway);
  font-weight: 600;
  background: #1f2123;
  color: #d6c1b0;
  width: 100%;

}

.search input[type=text]:focus {

  outline: none !important;
  box-shadow: 0 0 10px #719ECE;

}

.fa-spinner {
  margin-left: 10px;
  position: absolute;
  top: 50%;
}

.placeCard {
  position: relative;
  padding: 10px;
  width: 200px;
  height: 50px;
  border: var(--border);
  z-index: 200;
}


.current {
  width: 700px;
  display: flex;
  flex-direction: row;
  flex-basis: auto;
  gap: 10px;
  border: var(--border);
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0px 0px 4px 1px rgba(222, 215, 215, 0.692);
  justify-content: center;
}

.current-item {
  flex: 1;
  border: 1px solid rgb(62, 61, 61);
  padding: 10px;
}

.current-item:last-child {
  flex: 1;
}

.aqi {
  padding: 10px;
  width: 700px;
  display: flex;
  flex-direction: row;
  flex-basis: auto;
  gap: 10px;
  border: var(--border);
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0px 0px 4px 1px rgba(222, 215, 215, 0.692);
  justify-content: center;
}

.aqi>div {
  padding: 10px;
  /* flex: 1; */
  border: 0px solid red;

}

.heading-section {
  margin-top: "50px";
  float: left;
  display: flex;
  width: 100%;
  align-items: flex-end;
  gap: 10px;
}

.heading {
  margin-top: 20px;
  ;
  padding: 5px;
  height: 35px;
  border: var(--border);
  border-bottom: 0px;
  width: fit-content;
  border-radius: 10px 10px 0px 0px;
  background: linear-gradient(180deg,
      rgba(249, 211, 180, 1) 0%,
      rgba(249, 211, 180, 0.2) 50%);

  font-weight: 400;
  float: left;

}

.sub-heading {
  padding-block: 5px;
  margin-top: 35px;
  font-size: 1.2rem;
  flex-grow: 1;
  text-align: left;
}

.current .temp {
  font-size: 3.4rem;
  font-weight: 600;
  font-family: Google Sans, arial, sans-serif;
}

.current .temp span:nth-child(3) {
  top: -10px;
  font-size: 2rem;
  vertical-align: super;

}

.current .temp>div {
  font-size: 1rem;
  font-weight: 300;
}

.temp-maxmin {
  display: flex;
  font-size: 1rem;
  color: rgb(132, 128, 128);
}

.temp-maxmin>* {
  flex: 1;
}

.sun-moon {
  font-size: 1rem;
  color: rgb(132, 128, 128);
  display: flex;
  flex-direction: row;
  width: 100%;
  line-height: 1.8;
  border: 0px solid gray;
  margin-top: 25px;
}


.sun-moon>* {
  flex: 1;
  align-self: flex-end;
}

.sun-moon-item {
  display: flex;
  flex-direction: column;
}

.fa-sun {
  color: rgb(234, 181, 5);
}

.sun-moon-item i {
  color: rgb(225, 200, 135);
}

.condition {
  font-size: .8rem;
  margin-top: 0px;
}



.condition div span {
  color: rgb(234, 224, 19);
}

.right-side {
  display: flex;
  flex-direction: column;
}


.hourCards {
  width: 700px;
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  border-top: var(--border);
  overflow: auto;
}

.hourCard {
  margin: 5px 5px;
  font-size: 80%;
  padding: 5px;
  width: 100px;
  color: #f9d3b4;
  border: var(--border);
  border-radius: 10px;
  text-align: center;
  box-shadow: 0px 0px 4px 1px rgba(222, 215, 215, 0.692);
}

@media screen and (max-width: 600px) {
  .app {
    width: 36opx;
  }

  .search {
    width: 360px;
  }

  .current,
  .aqi {
    width: 360px;
    flex-direction: column;
  }

  .aqi {
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-start;

  }

  .aqi>div {
    border: 0px solid blue;
    width: fit-content;
    text-align: left;

  }

  .hourCards {
    width: 360px;
  }

  .sub-heading {
    display: none;
  }
}

.search-bar-container {
  width: 460px;
  margin: 0 auto;
  height: 52px;
  position: relative;
}

.search-bar {
  background: transparent;
  border: 1px solid #f6f6f6;
  font-size: 15px;
  padding-left: 40px;
  color: #4e5860;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  width: 100%;
  height: 100%;
}

.search-bar:focus {
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}

.search-bar-icon {
  position: absolute;
  left: 12px;
  top: 16px;
  font-size: 22px;
  color: #d4e457d1;
}

.line-height-1 {
  line-height: 1;
}

.search-result {
  position: absolute;
  top: 52px;
  width: 100%;
  max-height: 350px;
  overflow-y: auto;
  box-shadow: 0 5px 5px rgba(222, 215, 215, 0.692);
  color: #1c1d1f;


}

.list-group-item {
  cursor: pointer;
  background-color: #CEAF98;
}

.list-group-item.active,
.list-group-item:hover {
  background-color: #e5daa6;
  border-color: #7e8184;
  color: brown;
}

.loader {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #3498db;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  transform: translate(-50%, -50%);
  animation: spin 1s linear infinite;
  /* background: url('//upload.wikimedia.org/wikipedia/commons/thumb/e/e5/Phi_fenomeni.gif/50px-Phi_fenomeni.gif') 50% 50% no-repeat rgb(249, 249, 249); */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform: -webkit-translate(-50%, -50%);
  transform: -moz-translate(-50%, -50%);
  transform: -ms-translate(-50%, -50%);
  z-index: 9999;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* Absolute Center Spinner */
.loading {
  position: fixed;
  z-index: 999;
  overflow: show;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 50px;
  height: 50px;
}

/* Transparent Overlay */
.loading:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(59, 56, 56, 0.5);
}

/* :not(:required) hides these rules from IE9 and below */
.loading:not(:required) {
  /* hide "loading..." text */
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.loading:not(:required):after {
  content: '';
  display: block;
  font-size: 10px;
  width: 80px;
  height: 80px;
  margin-top: -0.5em;

  border: 15px solid rgb(243, 212, 33);
  border-radius: 100%;
  border-bottom-color: transparent;
  -webkit-animation: spinner 1s linear 0s infinite;
  animation: spinner 1s linear 0s infinite;


}

/* Animation */

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-moz-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-o-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}